table {
    width:80%;
    margin: auto;
    margin-top: 30px;
  }
  table, th, td {
    border: 1px solid black;
    border-collapse: collapse;

  }
  th, td {
    padding: 15px;
    text-align: left;
  }
  #t01 tr:nth-child(even) {
    background-color: #eee;
  }
  #t01 tr:nth-child(odd) {
   background-color: #fff;
  }
  #t01 th {
    background-color: black;
    color: white;
  }